/*
$font-small: 1.5588rem; // 18pt -> 23.94px
$font-medium: 1.6625rem; // 20pt -> 26.60px
$font-big: 2.66rem; // 32pt -> 42.56px
$font-xbig: 3.1587rem; // 38pt -> 50.54px
$font-lg: 3.7406rem; // 45pt -> 59.85 px
$font-xlg: 7.2319rem; // 87pt -> 115.71 px
*/
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  color: #FFFFFF;
  overflow-x: hidden;
}

.container {
  max-width: 70rem;
  margin: 0 auto;
}
.container--pall {
  padding: 4.375rem 1.5rem;
}
@media (min-width: 64em) {
  .container--pall {
    padding: 2.1875rem 1.5rem;
  }
}

.headline {
  font-weight: bold;
}

.headline--size-1 {
  font-size: 2.8125rem;
}
@media (min-width: 40em) {
  .headline--size-1 {
    font-size: 3.875rem;
  }
}
@media (min-width: 64em) {
  .headline--size-1 {
    font-size: 5.4375rem;
  }
}

.headline--size-2 {
  font-size: 2rem;
}
@media (min-width: 64em) {
  .headline--size-2 {
    font-size: 2.8125rem;
  }
}

.headline--yellow {
  color: #FFCC33;
}

.headline--blue {
  color: #03B5B0;
}

.text-center {
  text-align: center;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.hide-for-all {
  display: none;
}

.hero {
  background: url("../../assets/header-360.png") no-repeat center/cover;
  height: 35rem;
}
@media (min-width: 40em) {
  .hero {
    background: url("../../assets/header-1024.png") no-repeat center/cover;
  }
}
@media (min-width: 64em) {
  .hero {
    background: url("../../assets/header-1920.png") no-repeat center/cover;
    height: 45rem;
  }
}
.hero__logo {
  width: 140px;
  padding-top: 30px;
}
@media (min-width: 64em) {
  .hero__logo {
    width: 253px;
    padding-top: 120px;
  }
}
@media (min-width: 87.5em) {
  .hero__logo {
    padding-top: 100px;
  }
}
.hero__headline {
  padding-top: 95px;
}
.hero__text {
  font-size: 18px;
}
@media (min-width: 40em) {
  .hero__text {
    font-size: 25px;
  }
}
@media (min-width: 64em) {
  .hero__text {
    font-size: 30px;
  }
}

.list {
  background-color: #7A2B58;
  position: relative;
}
.list__headline--mobile {
  margin-bottom: 2rem;
}
.list__headline--desktop {
  color: #FFCC33;
}
.list__content {
  position: relative;
  border: 3px solid #FFFFFF;
  border-radius: 20px;
  padding: 16px;
  margin-top: 35px;
}
@media (min-width: 64em) {
  .list__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 37px;
    margin: 33px 99px;
  }
}
.list__text {
  font-size: 1rem;
  line-height: 1.2;
}
.list__text > p {
  margin-bottom: 1rem;
}
@media (min-width: 64em) {
  .list__text {
    font-size: 1.25rem;
    max-width: 30rem;
  }
}
@media (max-width: 63.9375em) {
  .list__image > img {
    position: absolute;
    top: -75px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.list__headline--mobile {
  margin-top: 4rem;
}
.list__icon--big {
  position: absolute;
  bottom: -40px;
  left: -170px;
}
.list__icon--small {
  position: absolute;
  bottom: -15px;
  left: -80px;
}

.dropdown {
  background-color: #03B5B0;
  position: relative;
}
.dropdown__headline {
  margin-bottom: 2rem;
}
.dropdown__text--white {
  font-size: 1rem;
  line-height: 1.2;
  margin-bottom: 2rem;
}
@media (min-width: 64em) {
  .dropdown__text--white {
    font-size: 1.25rem;
  }
}
.dropdown__cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 0 1.5rem;
}
@media (min-width: 64em) {
  .dropdown__cards {
    flex-direction: row;
    justify-content: space-between;
  }
}
.dropdown__card {
  width: 302px;
  height: 274px;
  background-color: #7A2B58;
  border-radius: 20px;
  overflow: hidden;
}
.dropdown__card-image {
  width: 302px;
  min-height: 234px;
  background-color: #FFFFFF;
}
.dropdown__card-image > img {
  display: block;
  width: 100%;
  top: 50px;
  left: 63px;
}
.dropdown__arrow {
  position: absolute;
  width: 48px;
  height: 54px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
.dropdown__card--open {
  background-color: #FFCC33;
}
.dropdown__text--purple {
  font-size: 1.125rem;
  line-height: 1.2;
  margin-bottom: 2rem;
}
.dropdown__icon {
  position: absolute;
  width: 144px;
  bottom: -15px;
  right: -70px;
}
@media (min-width: 64em) {
  .dropdown__icon {
    width: 335px;
    bottom: -40px;
    right: -180px;
  }
}

.grey-section {
  background-color: #353535;
}
.grey-section__headline {
  margin-bottom: 4.1875rem;
}
.grey-section__container {
  position: relative;
}
.grey-section__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 64em) {
  .grey-section__content {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 64em) {
  .grey-section__image {
    order: 0;
  }
}
.grey-section__image img {
  display: block;
  width: 100%;
}
.grey-section__text {
  max-width: 29rem;
  font-size: 20px;
}
@media (min-width: 64em) {
  .grey-section__text {
    order: -1;
  }
}
.grey-section__icon--blue {
  position: absolute;
  right: 340px;
  top: -40px;
}
.grey-section__icon--yellow {
  position: absolute;
  bottom: -30px;
  right: -15px;
}

.circle__content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 2rem 0;
  color: #707070;
}
.circle__content img {
  display: block;
  width: 100%;
}
@media (min-width: 40em) {
  .circle__content img {
    width: 500px;
  }
}

.footer {
  background-color: #7A2B58;
  padding: 3.5rem 0;
}